<template>
    <div class="todo">
        <div id="todoList">
            <div @keyup.enter="handleAddClick">
                <el-autocomplete
                    style="width: 100%"
                    popper-class="my-autocomplete"
                    v-model="newTodo"
                    ref="input"
                    :fetch-suggestions="querySearch"
                    placeholder="请输入内容，按下回车键确认"
                    @select="handleSelect">
                    <i class="el-icon-plus el-input__icon" style="cursor: pointer" slot="suffix" @click="handleAddClick"> </i>
                    <template slot-scope="{ item }">
                        <div class="name">{{ item.value }}</div>
                    </template>
                </el-autocomplete>
            </div>
            <div>
                <el-table ref="multipleTable" :data="todoList" tooltip-effect="dark" style="width: 100%; margin-top: 10px" @selection-change="handleSelectionChange">
                    <el-table-column type="selection" width="55"> </el-table-column>
                    <el-table-column label="待办事项">
                        <template slot-scope="scope"
                            ><span v-if="scope.row.isDone" style="text-decoration: line-through">{{ scope.row.content }}</span
                            ><span v-else>{{ scope.row.content }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column label="操作" width="90">
                        <template slot-scope="scope">
                            <el-button type="text" size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                            <el-button style="color: #f56c6c" type="text" size="mini" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <div style="display: flex; justify-content: space-between; align-items: center">
                    <p>
                        <span>已完成 </span><a style="color: #5cbb7a">{{ finishNum }}</a
                        ><span> / 全部 </span><a style="color: #8896b3">{{ allNum }}</a>
                    </p>
                    <el-button style="float: right; color: #e6a23c" type="text" @click="toggleSelection">清除已完成</el-button>
                </div>
                <el-dialog title="编辑待办" :visible.sync="centerDialogVisible" width="80%">
                    <el-input ref="nowInput" v-model="nowContent" placeholder="请输入新的内容"></el-input>
                    <span slot="footer" class="dialog-footer">
                        <el-button @click="centerDialogVisible = false">取 消</el-button>
                        <el-button type="primary" @click="handleUpdate">确 定</el-button>
                    </span>
                </el-dialog>
            </div>
        </div>
        <div id="timer" v-show="false"></div>
    </div>
</template>

<script>
    export default {
        name: "todoList",
        data() {
            return {
                suggestTodo: [],
                newTodo: "",
                todoList: [],
                finishNum: 0,
                centerDialogVisible: false,
                nowContent: "",
                nowContentID: "",
            }
        },
        computed: {
            allNum() {
                return this.todoList.length
            },
        },
        methods: {
            handleEdit(index, row) {
                this.centerDialogVisible = true
                this.nowContentID = row.uuid
                this.nowContent = row.content
                console.log(index, row)
            },
            handleUpdate() {
                this.todoList.forEach((todo) => {
                    if (todo.uuid == this.nowContentID) {
                        todo.content = this.nowContent
                    }
                })
                this.centerDialogVisible = false
            },
            handleDelete(index, row) {
                console.log(index, row)
                this.todoList = this.todoList.filter((todo) => todo.uuid != row.uuid)
            },
            saveToLocalStorage() {
                var todoStore = this.todoList.map((item) => {
                    return { content: item.content, isDone: item.isDone ? true : false, uuid: item.uuid ? item.uuid : this.generateUUID() }
                })
                window.localStorage.setItem("todoStore", JSON.stringify(todoStore))
            },
            generateUUID() {
                var d = new Date().getTime()
                var uuid = "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(/[xy]/g, function (c) {
                    var r = (d + Math.random() * 16) % 16 | 0
                    d = Math.floor(d / 16)
                    return (c == "x" ? r : (r & 0x7) | 0x8).toString(16)
                })
                return uuid
            },
            handleAddClick() {
                if (this.newTodo.trim()) {
                    this.todoList.push({ content: this.newTodo, isDone: false, uuid: this.generateUUID() })
                    this.newTodo = ""
                }
            },
            toggleSelection() {
                this.todoList = this.todoList.filter((todo) => todo.isDone === false)
            },
            handleSelectionChange(val) {
                console.log(111, val)
                this.multipleSelection = val
                this.finishNum = val.length
                this.todoList.forEach((todo) => {
                    if (val.findIndex((item) => item === todo) > -1) {
                        todo.isDone = true
                    } else {
                        todo.isDone = false
                    }
                })
            },
            querySearch(queryString, cb) {
                console.log(cb)
                var suggestTodo = this.suggestTodo
                var results = queryString ? suggestTodo.filter(this.createFilter(queryString)) : suggestTodo
                // 调用 callback 返回建议列表的数据
                cb(results)
            },
            createFilter(queryString) {
                return (restaurant) => {
                    return restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0
                }
            },
            loadAll() {
                return [{ value: "读一本书" }, { value: "看一部电影" }, { value: "学习一门新技术" }, { value: "锻炼半个小时" }, { value: "去看海" }]
            },
            handleSelect(item) {
                console.log(item)
                // this.$refs.input.focus()
            },
            handleIconClick(ev) {
                console.log(ev)
            },
        },
        beforeMount() {
            this.suggestTodo = this.loadAll()
            this.todoList = window.localStorage.getItem("todoStore") ? JSON.parse(window.localStorage.getItem("todoStore")) : []
        },
        mounted() {
            if (this.todoList.length > 0) {
                this.todoList.forEach((todo) => {
                    if (todo.isDone) this.$refs.multipleTable.toggleRowSelection(todo)
                })
            } else {
                this.$refs.multipleTable.clearSelection()
            }
        },
        updated() {
            this.saveToLocalStorage()
        },
    }
</script>

<style>
    @media screen and (max-width: 800px) {
        #todoList {
            position: absolute;
            left: 50%;
            margin-left: -40vw;
            width: 80vw;
        }
    }
    @media screen and (min-width: 800px) {
        #todoList {
            position: absolute;
            left: 50%;
            margin-left: -180px;
            width: 360px;
        }
    }

    .my-autocomplete {
        li {
            line-height: normal;
            padding: 7px;

            .name {
                text-overflow: ellipsis;
                overflow: hidden;
            }
            .addr {
                font-size: 12px;
                color: #b4b4b4;
            }

            .highlighted .addr {
                color: #ddd;
            }
        }
    }
</style>
